import Tweet from "./tweet";

const Timeline = (props) =>{
    const tweetList = props.tweets.map((tw)  => {
    
    return(
        <>
        <Tweet
            Key={tw.id}
            icon={tw.icon}
            displayName={tw.displayName}
            accountName={tw.accountName}
            content={tw.content}
        />
        </>
    )
});

return (
    <div className="timeline">{ tweetList }</div>
)
}


export default Timeline;